body {
  margin: 0;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 14px;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* Header Style */
.breadcrumb {
  background-color: #272A42; 
  height: 50px;
}
.logo{
  height: 85%;
}
/* Each Comment Style */
.comment {
  border: 1px solid;
  border-color:gray;
  margin: 10px;
}

.comment h5 {
  text-align: left;
  margin: 10px;
}

.comment p {
  text-align: left;
  margin-left: 10px;
}

.close{
  margin-right: 10px;
}

/* Form Style */
.form {
  background-color: #F5F7FA;
}

.mybtn {
  margin-bottom: 10px;
  background-color: #337AB7;
  color: #fff;
}

.form-title {
  font-size: 24px;
  text-align: left;
}